import React, { forwardRef } from "react";
import PropTypes from "prop-types";
import "./Receipt.css";

// Helper function to format numbers with commas
const formatNumber = (num) => {
  return Number(num).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });
};

const Receipt = forwardRef(({
  orderNumber = "N/A",
  date = new Date().toLocaleDateString(),
  items = [],
  subtotal = 0,
  discount = 0,
  total = 0,
  amountTendered = 0,
  change = 0,
  customerName = "",
  tableNo = "",
  vatableSales = 0,
  vatAmount = 0,
  nonVatableSales = 0
}, ref) => {
  // Calculate totals for display
  const displaySubtotal = subtotal;
  const displayTotal = total;
  const hasVatItems = vatableSales > 0;
  const hasNonVatItems = nonVatableSales > 0;

  return (
    <div ref={ref} className="receipt-container">
      <div className="receipt">
        <div className="receipt-header text-center">
          <h4 className="receipt-title">KER IT SOLUTIONS</h4>
          <p className="receipt-address">Poblacion, Mabinay, Negros Oriental</p>
          <p className="receipt-contact">+63 906 308 4301</p>
        </div>

        <div className="receipt-order-info">
          <div className="d-flex justify-content-between">
            <span>Invoice #:</span>
            <span>{orderNumber}</span>
          </div>
          <div className="d-flex justify-content-between">
            <span>Staff:</span>
            <span>KERR DEVERO</span>
          </div>
          <div className="d-flex justify-content-between">
            <span>Date:</span>
            <span>{date}</span>
          </div>
          {customerName && (
            <div className="d-flex justify-content-between">
              <span>Customer:</span>
              <span>{customerName}</span>
            </div>
          )}
          {tableNo && (
            <div className="d-flex justify-content-between">
              <span>Table #:</span>
              <span>{tableNo}</span>
            </div>
          )}
        </div>

        <hr className="receipt-divider" />

        <div className="receipt-items">
          {items.map((item, index) => {
            const price = Number(item.price) || 0;
            const qty = Number(item.quantity) || 0;
            const itemTotal = price * qty;
            const unitName = item.type === 'retail' ? item.retail_unit_name : item.wholesale_unit_name;

            return (
              <div key={index} className="receipt-item">
                <div className="d-flex justify-content-between">
                  <span className="receipt-item-name">
                    {qty} {unitName} {item.name}
                  </span>
                  <span className="receipt-item-price">
                    ₱{formatNumber(itemTotal)}
                  </span>
                </div>
                <div className="d-flex justify-content-between">
                  <small className="text-muted">
                    @ ₱{formatNumber(price)}/{unitName}
                  </small>
                </div>
              </div>
            );
          })}
        </div>

        <hr className="receipt-divider" />

        <div className="receipt-totals">
          <div className="d-flex justify-content-between">
            <span>Subtotal:</span>
            <span>₱{formatNumber(displaySubtotal)}</span>
          </div>

          {/* VAT Breakdown - only show if there are VATable items */}
          {hasVatItems && (
            <>
              <div className="d-flex justify-content-between">
                <small className="text-muted">VATable Sales:</small>
                <small className="text-muted">₱{formatNumber(vatableSales)}</small>
              </div>
              <div className="d-flex justify-content-between">
                <small className="text-muted">VAT (12%):</small>
                <small className="text-muted">₱{formatNumber(vatAmount)}</small>
              </div>
            </>
          )}

          {/* Non-VAT Breakdown - only show if there are non-VATable items */}
          {hasNonVatItems && (
            <div className="d-flex justify-content-between">
              <small className="text-muted">Non-VATable Sales:</small>
              <small className="text-muted">₱{formatNumber(nonVatableSales)}</small>
            </div>
          )}

          <div className="d-flex justify-content-between">
            <span>Discount:</span>
            <span>-₱{formatNumber(discount)}</span>
          </div>

          <div className="d-flex justify-content-between">
            <strong>Total Amount:</strong>
            <strong>₱{formatNumber(displayTotal)}</strong>
          </div>

          <div className="d-flex justify-content-between">
            <span>Cash:</span>
            <span>₱{formatNumber(amountTendered)}</span>
          </div>

          <div className="d-flex justify-content-between">
            <span>Change:</span>
            <span>₱{formatNumber(change)}</span>
          </div>
        </div>

        <hr className="receipt-divider" />

        <div className="receipt-footer text-center">
          <p>Thank you for your purchase!</p>
          <small>Please come again</small>
        </div>
      </div>
    </div>
  );
});

Receipt.propTypes = {
  orderNumber: PropTypes.string,
  date: PropTypes.string,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      type: PropTypes.string,
      price: PropTypes.number.isRequired,
      quantity: PropTypes.number.isRequired,
      vatable: PropTypes.number,
      retail_unit_name: PropTypes.string,
      wholesale_unit_name: PropTypes.string,
    })
  ),
  subtotal: PropTypes.number,
  discount: PropTypes.number,
  total: PropTypes.number,
  amountTendered: PropTypes.number,
  change: PropTypes.number,
  customerName: PropTypes.string,
  tableNo: PropTypes.string,
  vatableSales: PropTypes.number,
  vatAmount: PropTypes.number,
  nonVatableSales: PropTypes.number,
};

export default Receipt;