// Orders.jsx
import React, { useRef, useState, useEffect } from "react";
import { useReactToPrint } from "react-to-print";
import axios from "axios";
import { format, parseISO } from "date-fns";
import { useNavigate } from "react-router-dom";

const SalesTable = () => {
  const [sales, setSales] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [dateFilter, setDateFilter] = useState(format(new Date(), "yyyy-MM-dd"));
  const [error, setError] = useState(null);
  const componentRef = useRef();
  const navigate = useNavigate();

  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    documentTitle: `Sales_Report_${dateFilter}`,
    pageStyle: `
      @page { size: auto; margin: 5mm; }
      @media print {
        body { font-size: 12px; }
        table { width: 100%; }
        .badge { color: white !important; }
        .card-header, .card-tools { display: none; }
      }
    `,
  });

  useEffect(() => {
    const fetchSales = async () => {
      try {
        setLoading(true);
        setError(null);
        const response = await axios.get(
          `${process.env.REACT_APP_API_BASE_URL}/api/sales/${dateFilter}`
        );
        setSales(response.data?.data || response.data || []);
      } catch (err) {
        console.error("Error fetching sales:", err);
        setError(err.response?.data?.message || "Failed to fetch sales data");
        setSales([]);
      } finally {
        setLoading(false);
      }
    };

    fetchSales();
  }, [dateFilter]);

  // ✅ Updated to use React Router instead of window.location
  const handleRowClick = (saleId) => {
    navigate(`/edit-sales/${saleId}`);
  };

  const filteredSales = sales.filter(
    (sale) =>
      (sale.transaction_number?.toLowerCase() || "").includes(
        searchTerm.toLowerCase()
      ) ||
      (sale.customer?.toLowerCase() || "").includes(searchTerm.toLowerCase()) ||
      (sale.table_no?.toLowerCase() || "").includes(searchTerm.toLowerCase())
  );

  const getStatusBadge = (status) => {
    switch (status) {
      case 1:
        return <span className="badge bg-success">paid</span>;
      case 2:
        return <span className="badge bg-warning">unpaid</span>;
      default:
        return null;
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat("en-PH", {
      style: "currency",
      currency: "PHP",
      minimumFractionDigits: 2,
    }).format(amount || 0);
  };

  const formatDate = (dateString) => {
    try {
      return format(parseISO(dateString), "MMM d, yyyy");
    } catch {
      return dateString;
    }
  };

  const formatTime = (dateString) => {
    try {
      return format(parseISO(dateString), "h:mm a");
    } catch {
      return "";
    }
  };

  const calculateTotals = (field) => {
    return filteredSales.reduce((sum, sale) => sum + (sale[field] || 0), 0);
  };

  return (
    <div className="col-12">
      <div className="card">
        <div className="card-header">
          <h3 className="card-title">Daily Sales</h3>

          <div className="card-tools">
            <div className="input-group input-group-sm" style={{ width: 300 }}>
              <input
                type="date"
                className="form-control"
                value={dateFilter}
                onChange={(e) => setDateFilter(e.target.value)}
                max={format(new Date(), "yyyy-MM-dd")}
              />
              <input
                type="text"
                className="form-control float-right"
                placeholder="Search transactions..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-default"
                  onClick={handlePrint}
                  disabled={loading || filteredSales.length === 0}
                >
                  <i className="fas fa-print"></i>
                </button>
              </div>
            </div>
          </div>
        </div>

        <div className="card-body table-responsive p-0" style={{ height: 600 }}>
          <div ref={componentRef}>
            <div className="p-3">
              <p className="mb-2">
                <strong>Date:</strong> {formatDate(dateFilter)}
              </p>
              <p className="mb-0">
                <strong>Total Transactions:</strong> {filteredSales.length}
              </p>
            </div>

            <table className="table table-head-fixed text-nowrap">
              <thead>
                <tr>
                  <th>Transaction #</th>
                  <th width="200">Date/Time</th>
                  <th>Customer</th>
                  <th className="text-end">Total</th>
                  <th className="text-end">Discount</th>
                  <th className="text-end">Amount Paid</th>
                  <th className="text-end">Change</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {loading ? (
                  <tr>
                    <td colSpan="8" className="text-center py-4">
                      <div
                        className="spinner-border text-primary"
                        role="status"
                      >
                        <span className="sr-only">Loading...</span>
                      </div>
                    </td>
                  </tr>
                ) : error ? (
                  <tr>
                    <td colSpan="8" className="text-center text-danger py-4">
                      {error}
                    </td>
                  </tr>
                ) : filteredSales.length === 0 ? (
                  <tr>
                    <td colSpan="8" className="text-center py-4">
                      No sales found for this date
                    </td>
                  </tr>
                ) : (
                  filteredSales.map((sale) => (
                    <tr
                      key={sale.id}
                      onClick={() => handleRowClick(sale.id)}
                      style={{ cursor: "pointer" }}
                      className="hover-row"
                    >
                      <td>{sale.transaction_number || "-"}</td>
                      <td>
                        {formatDate(sale.date)}
                        <br />
                        <small>{formatTime(sale.created_at)}</small>
                      </td>
                      <td>{sale.customer || "Walk-in"}</td>
                      <td className="text-end">
                        {formatCurrency(sale.total)}
                      </td>
                      <td className="text-end">
                        {formatCurrency(sale.discount)}
                      </td>
                      <td className="text-end">
                        {formatCurrency(sale.amt_tendered)}
                      </td>
                      <td className="text-end">
                        {formatCurrency(sale.amount_change)}
                      </td>
                      <td>{getStatusBadge(sale.status)}</td>
                    </tr>
                  ))
                )}
              </tbody>
              {!loading && !error && filteredSales.length > 0 && (
                <tfoot>
                  <tr className="bg-light">
                    <td colSpan="3" className="text-end">
                      <strong>Totals:</strong>
                    </td>
                    <td className="text-end">
                      <strong>{formatCurrency(calculateTotals("total"))}</strong>
                    </td>
                    <td className="text-end">
                      <strong>
                        {formatCurrency(calculateTotals("discount"))}
                      </strong>
                    </td>
                    <td className="text-end">
                      <strong>
                        {formatCurrency(calculateTotals("amt_tendered"))}
                      </strong>
                    </td>
                    <td className="text-end">
                      <strong>
                        {formatCurrency(calculateTotals("amount_change"))}
                      </strong>
                    </td>
                    <td></td>
                  </tr>
                </tfoot>
              )}
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SalesTable;
